body, h1, p, a, button {
  margin: 0;
  padding: 0;
}

body {
  text-align: center;
  background-color: #f0f0f0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

button[data-start], button[data-stop] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button[data-start]:disabled, button[data-stop]:disabled {
  cursor: not-allowed;
  color: #666;
  background-color: #ccc;
}

button[data-start]:hover, button[data-stop]:hover {
  background-color: #0056b3;
}

